.demo {
    width: 100%;
    height: 80vh;
    display: flex;
    justify-content: space-evenly;
}

.demo-left {
    width: 40%;
    height: 90%;
    background-color: rgb(255, 243, 243);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.demo-left img {
    width: 70%;
    height: auto;
    object-fit: contain;
}

.demo-right {
    width: 40%;
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.demo-right-container-1 {
    width: 100%;
    height: 8vh;
    margin-left: 12rem;
}

.demo-right-container-1 button {
    width: 10rem;
    height: 100%;
    font-size: large;
    background-color: white;
    border: none;
}

.demo-right-container-2 {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

}

.democard {
    width: 80%;
    height: 15vh;
    background-color: white;
    border: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 15px;
}

.democard-1 {
    width: 30%;
    height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.democard-1 img {
    width: 100%;
    height: auto;
    object-fit: contain;
}

.democard-2 {
    width: 50%;
    height: 80%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;

}

.democard-2 p {
    color: gray;
}

.democard-2 h2 {
    font-weight: 100;
}